@charset "UTF-8";
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-width: 100%;
  max-width: 100%;
  font-family: "Nunito", "Arial", sans-serif;
  margin: 0;
  padding: 0;
  font-size: 16px;
}

.page__main {
  flex-grow: 1;
}

.page__header-wrapper {
  position: relative;
  margin: 0;
  padding: 0 20px;
  padding-bottom: 78px;
}
.page__header-wrapper::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-image: url(../img/header-background.webp);
  background-color: #323a27;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top 73% left 50%;
  transform: scaleX(-1);
}

.page__header-container {
  max-width: 1160px;
  margin: 0 auto;
  padding-top: 22px;
  color: #ffffff;
}

.page__header {
  margin-bottom: 65px;
}

.page__section {
  box-sizing: border-box;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.page__how {
  margin-bottom: calc(35px + 5.7vw);
}

.page__opportunities {
  margin-bottom: 60px;
}

.page__join {
  padding: 0 20px;
  margin-bottom: 60px;
}

.page__faq {
  margin-bottom: 108px;
}

.page__footer {
  box-sizing: border-box;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px 22px 20px;
}

@media (max-width: 880px) {
  .page__body {
    font-size: 12px;
  }
  .page__header-container {
    max-width: 880px;
    min-width: 340px;
    padding-top: 28px;
  }
  .page__header {
    margin-bottom: 30px;
  }
  .page__section {
    min-width: 380px;
    padding: 0;
  }
  .page__how {
    margin-bottom: 9vw;
  }
  .page__opportunities {
    margin-bottom: 30px;
  }
  .page__join {
    padding: 0 20px;
    margin-bottom: 35px;
  }
  .page__faq {
    padding: 0 20px;
    margin-bottom: 35px;
  }
}
.main-header {
  display: flex;
  align-items: center;
}

.main-header__nav-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: auto;
}

.main-header__logo {
  display: block;
  margin-right: 35px;
}
.main-header__logo:hover {
  box-shadow: 0 0 15px 0 #ffa200;
}
.main-header__logo:active {
  opacity: 0.5;
}

.main-header__logo-image {
  display: block;
  margin: 0;
  padding: 0;
  width: 200px;
  height: 43px;
}

.main-header__nav {
  align-self: flex-start;
}

.main-header__language-wrapper {
  box-sizing: border-box;
  position: relative;
  width: 117px;
  margin: 0;
  margin-left: 28px;
}
.main-header__language-wrapper:hover .main-header__select-icon {
  color: #ffa200;
}

.main-header__language {
  box-sizing: border-box;
  margin: 0;
  padding: 9px;
  padding-right: 21.5px;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  color: #ffffff;
  font-family: "Nunito", "Arial", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.2px;
  appearance: none;
  cursor: pointer;
}
.main-header__language:hover {
  color: #ffa200;
  border-color: #ffa200;
}

.main-header__select-icon {
  position: absolute;
  left: 85px;
  top: 50%;
  z-index: 0;
  color: #ffffff;
  transform: translateY(-50%);
}

.main-header__login {
  padding: 18px 0 13px 15px;
  flex-shrink: 0;
}

.main-header__login-icon {
  color: #ffffff;
  border-radius: 50%;
}
.main-header__login-icon:hover {
  color: #ffa200;
  box-shadow: 0 0 15px 0 rgba(255, 255, 255, 0.7);
}
.main-header__login-icon:active {
  opacity: 0.5;
}

.main-header__navigation-toggle {
  display: none;
}

@media (max-width: 880px) {
  .main-header__logo {
    margin: 0;
  }
  .main-header__logo-image {
    width: 203px;
    height: 44px;
  }
  .main-header__nav-wrapper {
    position: absolute;
    z-index: 10;
    top: 88px;
    left: 0;
    width: 100%;
    padding: 15px 0 20px 0;
    flex-direction: column;
    align-items: center;
    backdrop-filter: blur(10px);
    background-color: rgba(255, 255, 255, 0.7);
  }
  .main-header__nav-wrapper--closed {
    display: none;
  }
  .main-header__nav-wrapper--opened {
    display: block;
  }
  .main-header__nav {
    align-self: center;
    margin: 0;
    padding: 0;
  }
  .main-header__language-wrapper {
    align-self: center;
    margin: 0 auto;
    margin-top: 16px;
    padding: 0;
  }
  .main-header__language {
    width: 140px;
    margin: 0;
    color: #000000;
    border-color: #000000;
  }
  .main-header__select-icon {
    left: 107px;
    top: 50%;
    color: #000000;
    transform: translateY(-50%);
  }
  .main-header__login {
    margin: 0;
    margin-top: 3px;
    margin-left: auto;
    padding: 6px 17px;
  }
  .main-header__navigation-toggle {
    position: relative;
    z-index: 2;
    display: block;
    box-sizing: border-box;
    flex-shrink: 0;
    width: 28px;
    height: 28px;
    margin-left: 5px;
    padding: 10px 17px;
    background: transparent;
    border: none;
    cursor: pointer;
  }
  .main-header__navigation-toggle--burger::before {
    content: "";
    position: absolute;
    left: 0;
    top: 8px;
    width: 18px;
    height: 1px;
    background-color: #ffffff;
    box-shadow: 0 6px 0 0 #ffffff, 0 12px 0 0 #ffffff;
  }
  .main-header__navigation-toggle--burger:hover::before {
    background-color: #ffa200;
    box-shadow: 0 6px 0 0 #ffa200, 0 12px 0 0 #ffa200;
  }
  .main-header__navigation-toggle--burger:active::before {
    opacity: 0.5;
  }
  .main-header__navigation-toggle--cross::before, .main-header__navigation-toggle--cross::after {
    content: "";
    position: absolute;
    left: 0;
    top: 13px;
    width: 18px;
    height: 1px;
    background-color: #ffffff;
    transform: rotate(45deg);
  }
  .main-header__navigation-toggle--cross::after {
    transform: rotate(-45deg);
  }
  .main-header__navigation-toggle--cross:hover::before, .main-header__navigation-toggle--cross:hover::after {
    background-color: #ffa200;
  }
  .main-header__navigation-toggle--cross:active::before, .main-header__navigation-toggle--cross:active::after {
    opacity: 0.5;
  }
}
.main-nav {
