.diagram {
  padding-top: 0;
  padding-bottom: 10px;
  padding-left: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(-10px);
}

.diagram__level {
  position: relative;
  box-sizing: border-box;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.diagram__level--5 {
  width: 542px;
  height: 542px;
  border: 1px solid rgba(3, 186, 254, 0.15);
}
.diagram__level--5::after {
  content: "";
  position: absolute;
  right: 16.5%;
  bottom: 2.5%;
  display: block;
  width: 10%;
  height: 10%;
  border: 2px solid #ffffff;
  border-radius: 50%;
  background-image: url("../img/diagram-3.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transform: translate(-50%, -50%);
}
.diagram__level--4 {
  width: 85%;
  height: 85%;
  background-color: rgba(94, 211, 255, 0.4);
}
.diagram__level--4::after {
  content: "";
  position: absolute;
  left: 39%;
  top: 13%;
  display: block;
  width: 12%;
  height: 12%;
  border: 2px solid #ffffff;
  border-radius: 50%;
  background-image: url("../img/diagram-1.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transform: translate(-50%, -50%);
}
.diagram__level--3 {
  width: 80%;
  height: 80%;
  background-color: rgba(94, 211, 255, 0.4);
}
.diagram__level--3::after {
  content: "";
  position: absolute;
  left: 25%;
  bottom: -8.5%;
  display: block;
  width: 15%;
  height: 15%;
  border: 2px solid #ffffff;
  border-radius: 50%;
  background-image: url("../img/diagram-2.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transform: translate(-50%, -50%);
}
.diagram__level--2 {
  width: 73%;
  height: 73%;
  background-color: rgba(3, 186, 254, 0.2);
}
.diagram__level--1 {
  width: 66%;
  height: 66%;
  background-color: #ffa200;
}
.diagram__level--you {
  width: 47%;
  height: 47%;
  background-color: #ffffff;
  background-image: url("../img/diagram-you.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border: 2px solid #ffffff;
  border-radius: 50%;
}

.diagram-level__tooltip {
  position: absolute;
  z-index: 3;
  width: max-content;
  padding: 15px;
  background-color: #ffffff;
  box-shadow: 0px 24px 32px rgba(50, 50, 71, 0.05);
  transition: transform 500ms ease;
}
.diagram-level__tooltip--you {
  bottom: 65%;
  right: 55%;
  padding-right: 4vw;
  border-radius: 20px 20px 0 20px;
}
.diagram-level__tooltip--1 {
  left: 68%;
  bottom: 78%;
  border-radius: 20px 20px 20px 0;
  transform-origin: bottom left;
}
.diagram-level__tooltip--2 {
  right: 90%;
  top: 62%;
  border-radius: 20px 0px 20px 20px;
  transform-origin: top right;
}
.diagram-level__tooltip--3 {
  left: 70%;
  bottom: 12%;
  border-radius: 20px 20px 20px 0;
  transform-origin: bottom left;
}
.diagram-level__tooltip--4 {
  right: 50%;
  top: 95%;
  border-radius: 20px 0px 20px 20px;
  transform-origin: top right;
}
.diagram-level__tooltip--closed {
  visibility: hidden;
  transform: scale(0.1);
}
.diagram-level__tooltip--opened {
  visibility: visible;
  display: block;
  transform: scale(1);
}

.diagram-level__button {
  position: absolute;
  z-index: 2;
  max-width: 30px;
  max-height: 30px;
  width: 2.6vw;
  height: 2.6vw;
  border: 1px solid #ffffff;
  border-radius: 50%;
  cursor: pointer;
}
.diagram-level__button--1 {
  left: 60%;
  bottom: 70%;
  background-color: rgba(255, 255, 255, 0.7);
}
.diagram-level__button--2 {
  left: 4.5%;
  bottom: 34%;
  background-color: #5bc9f1;
}
.diagram-level__button--3 {
  right: 26%;
  bottom: 8%;
  background-color: #6bcbef;
}
.diagram-level__button--4 {
  right: 47%;
  bottom: 2%;
  background-color: rgba(107, 203, 239, 0.2);
}
.diagram-level__button:hover {
  background-color: #03bafe;
}
.diagram-level__button:active {
  opacity: 0.5;
}

@media (min-width: 880px) and (max-width: 1160px) {
  .diagram__level--5 {
    width: 47vw;
    height: 47vw;
  }
  .diagram-level__tooltip {
    max-width: 18vw;
  }
}
@media (max-width: 880px) {
  .diagram__level--5 {
    width: 64vw;
    height: 64vw;
  }
  .diagram-level__tooltip {
    width: max-content;
    padding: 10px;
  }
  .diagram-level__button {
    width: 4vw;
    height: 4vw;
  }
}
