  .faq__button {
    width: 32px;
    height: 32px;
  }
  .faq__description {
    padding-right: 40px;
    font-size: 12px;
    line-height: 24px;
  }
}
.footer__title {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  text-align: center;
  font-family: "Manrope", Arial, sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
}

.footer__emails {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  margin-bottom: 12px;
  padding-bottom: 30px;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #f9f9f9;
}

.footer__link {
  display: block;
  margin: 0;
  margin-right: 48px;
  padding: 10px;
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  color: #03bafe;
}
.footer__link:hover {
  color: #0068ff;
}
.footer__link:active {
  opacity: 0.5;
}

.footer__other-links {
  margin-bottom: 54px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.footer__files {
  display: flex;
  flex-wrap: wrap;
  max-width: 50%;
  transform: translateX(-20px);
}
.footer__files .footer__link {
  margin-right: 20px;
}

.footer__socials {
  flex-shrink: 0;
}

.footer__socials-list {
  margin: 0;
}

.footer__copyright {
  margin: 0;
  padding: 0 20px;
  text-align: center;
  font-weight: 400;
  font-size: 12px;
  line-height: 24px;
  color: #666666;
}

@media (max-width: 880px) {
  .footer__title {
    text-align: left;
    margin-bottom: 10px;
  }
  .footer__emails {
    margin-bottom: 30px;
    padding: 0;
    justify-content: space-between;
    border: none;
  }
  .footer__link {
    margin: 0;
    text-decoration: none;
  }
  .footer__other-links {
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;
    margin-bottom: 20px;
  }
  .footer__files {
    display: block;
    max-width: 100%;
    margin: 0;
    padding: 0;
    grid-row: 2/3;
    text-align: center;
    transform: translate(0);
  }
  .footer__files .footer__link {
    margin: 0;
    padding: 0;
  }
}
.social {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  padding-right: 10px;
  list-style: none;
}

.social__item {
  width: 30px;
  height: 30px;
  padding: 10px 4px;
  flex-shrink: 0;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #f3f3f3;
  color: #000000;
}
.social__link:hover {
  background-color: rgba(235, 250, 255, 0.7);
  color: #03bafe;
}
.social__link:active {
  opacity: 0.5;
}

.social__icon {
  color: inherit;
}

.block-title {
  font-weight: 700;
  font-size: calc(23px + 1.2vw);
  letter-spacing: -0.025em;
  line-height: 125%;
}

@media (min-width: 1440px) {
  .block-title {
    font-size: 40px;
  }
}
@media (max-width: 880px) {
  .block-title {
    font-size: calc(17px + 1.2vw);
    line-height: 115%;
  }
}